.WorkWrapper {
	padding-left: 0px;
	padding-bottom: 0px;
	background-color:  #2ecc71;
}

.WorkWrapper h1 {
	font-family: MyriadProRegular;
	color: #fff;
	font-size: 30px;
	padding-top: 39px;
	padding-bottom: 39px;
	text-align: left;
	text-transform: uppercase;
	margin-bottom: 50px;
}

.Works {
	font-family: MyriadProRegular;
	display: grid;
	grid-template-columns: 90% 10%;
	margin-top: 50px;
	margin-bottom: 50px;
}

.Works button {
	border: transparent;
	background-color: transparent;
	font-family: Novecentosanswide;
	color: #8a8888;
}

.slash {
	font-size: 18px;
	font-weight: 900;
	color: #8a8888;
	margin-right: 10px;
	margin-left: 10px;
}

.slash:nth-child(5) {
	display: none;
}

.GridListView {
	justify-self: end;
}

.GridListView button:nth-child(1) {
	margin-right: 10px
}

.FilteredView {
	justify-self: start;
}

.active {
	color: #2ecc71;
}

.FilteredView div button {
	-webkit-transition: color .3s;
	-o-transition: color .3s;
	transition: color .3s; 
}

.FilteredView div button:hover, .FilteredView div button:focus {
	color: #2ecc71;
}

.FilteredView div button:nth-child(5) span {
	display: none;
}

.Preview {
	list-style: none;
	display: grid;
	grid-template-columns: auto auto auto;
	grid-column-gap: 20px;
	grid-row-gap: 20px;
	margin-bottom: 50px;
}

#list, #grid {
	fill: #737373;
	-webkit-transition: fill .3s;
	-o-transition: fill .3s;
	transition: fill .3s;
}

button:hover #list, button:focus #list, button:hover #grid, button:focus #grid {
	fill: #2ECC71;
}

.List {
	display: block;
	list-style: none;
}

.List li {
	text-align: left;
	max-width: 300px;
	margin-bottom: 20px;
}

.Preview a li img {
	width: 100%;
	height: auto;
}

.Preview a li .ImageWrapper {
	position: relative;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	z-index: 99;
}

.Preview a li:hover .OverlayWrapper {
	opacity: 1;
}

.Preview a li .OverlayWrapper {
	position: absolute;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	z-index: 100;
	opacity: 0;
	-webkit-transition: opacity .3s;
	-o-transition: opacity .3s;
	transition: opacity .3s;
}

.Preview a li .OverlayWrapper .Link {
	position: absolute;
	top: 50%;
  	left: 50%;
  	-webkit-transform: translate(-50%, -50%);
  	    -ms-transform: translate(-50%, -50%);
  	        transform: translate(-50%, -50%);
  	width: auto;
}

.show {
  	display: block!important;
}

.element {
	display: none;
}

.Preview li:nth-of-type(1) {
	grid-column: 1 / 2;
    grid-row: 1 / 2;
}

.Preview li:nth-of-type(2) {
	grid-column: 2 / 3;
    grid-row: 1 / 2;
}

.Preview li:nth-of-type(3) {
	grid-column: 3 / 4;
    grid-row: 1 / 2;
}

.Preview li:nth-of-type(4) {
	grid-column: 1 / 2;
    grid-row: 2 / 3;
}

.Preview li:nth-of-type(5) {
	grid-column: 2 / 3;
    grid-row: 2 / 3;
}

.Preview li:nth-of-type(6) {
	grid-column: 3 / 4;
    grid-row: 2 / 3;
}

.Preview li:nth-of-type(7) {
	grid-column: 1 / 2;
    grid-row: 3 / 4;
}

.Preview li:nth-of-type(8) {
	grid-column: 2 / 3;
    grid-row: 3 / 4;
}

.Preview li:nth-of-type(9) {
	grid-column: 3 / 4;
    grid-row: 3 / 4;
}

@media screen and (max-width: 479px) {
	.container-fluid:nth-child(3) {
		padding-left: 5px;
		padding-right: 5px;
	}

	.container {
		margin: 0 auto;
	}

	.Preview {
		-ms-flex-pack: center;
		    justify-content: center;
	}

	.Preview a li .OverlayWrapper .Link {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		    -ms-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		width: auto;
		height: 60%;
	}

	.Preview a li img:first-child {
		width: 85%;
	}
	
	.GridListView {
		justify-self: center;
	}

	.GridListView button:nth-child(1) {
		width: 10px;
		padding-right: 3px;
	}
}


